<template>
  <div class="h-screen w-full flex flex-col items-center justify-center bg-background p-4">
    <div class="text-center space-y-6 max-w-md">
      <!-- Error Graphic/Text -->
      <div class="space-y-2">
        <h1 class="text-7xl font-extrabold tracking-tighter text-destructive">Error</h1>
        <h2 class="text-2xl font-bold tracking-tight text-foreground">Something went wrong</h2>
        <p class="text-muted-foreground">
          {{ errorMessage || "An unexpected error occurred. Please try again later or contact support." }}
        </p>
      </div>

      <!-- Actions -->
      <div class="flex gap-4 justify-center">
        <button 
          @click="router.push('/')"
          class="inline-flex items-center justify-center whitespace-nowrap rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-primary text-primary-foreground hover:bg-primary/90 h-10 px-4 py-2"
        >
          Go to Dashboard
        </button>
        <button 
          @click="reloadPage"
          class="inline-flex items-center justify-center whitespace-nowrap rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 border border-input bg-background hover:bg-accent hover:text-accent-foreground h-10 px-4 py-2"
        >
          Try Again
        </button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { computed } from 'vue'
import { useRouter, useRoute } from 'vue-router'

const router = useRouter()
const route = useRoute()

const errorMessage = computed(() => route.query.message)

const reloadPage = () => {
    window.location.reload()
}
</script>
